<template>
  <div>
    <v-snackbar dark v-model="snackbar" :right="true" :timeout="timeout" :top="top" :bottom="!top" :multi-line="false ">
      {{ text }}
      <v-btn text style="color:#FF5722;" @click="snackbar = false"> &nbsp; 确认 &nbsp; </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      text: "",
      timeout: 3000,
      snackbar: false,
      top: true
    };
  },
  methods: {
    getProductCheckResult(options) {
      this.text = options.text;
      this.timeout = options.timeout;
      this.snackbar = options.snackbar;
      this.top = options.top;
    }
  }
};
</script>