import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './registerServiceWorker'
import vuetify from './plugins/vuetify';
/* 提示框组件 */
import getProductCheckResult from "./plugins/promptBox/promptBox.js";
Vue.use(getProductCheckResult);

/* loading */
import loading from './plugins/loading'
Vue.use(loading);

// @decs 全局拦截每一次路由跳转
// @decs 设置页头标题，Tab状态
router.beforeEach((to, from, next) => {
    if (to.matched.length === 0) {
        from.name ? next({ name: from.name }) : next('/Tab1');
    } else {
        console.log(to);
        store.commit("newTitle", to.meta.titleHeader);
        to.meta.TabStatus ? store.commit("newShowTab", to.meta.titleHeader) : store.commit("newHideTab", to.meta.titleHeader);
        next();
    }

});
/* axios */
import axios from './axios';
/* 过滤器 */
import * as filters from './server/filter.js';
Object.keys(filters).forEach(key => {
    Vue.filter(key, filters[key]);
});
/* API */
import Apis from "./server/api.js";
Vue.prototype.$apis = Apis;

/* Cookie */
import Cookie from './server/cookies.js';
Vue.prototype.$cookie = Cookie;



Vue.config.productionTip = false



new Vue({
    router,
    axios,
    store,
    vuetify,
    render: h => h(App)
}).$mount('#app')