import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate';
Vue.use(Vuex)

/* 要设置的全局访问的state对象 */
const state = {
    /* 要设置的初始属性值 */
    // @desc header 标题
    titleHeader: {
        type: '',
        text: ''
    },
    // @decs tab状态
    showTab: true,

    showFooter: true,
    changableNum: 18,
    userData: {},
    paht: {}
};

/* 实时监听state值的变化(最新状态) */
const getters = {
    // @decs 获取页头标题状态
    getTitleHeader(state) {
        return state.titleHeader
    },
    // @decs 获取tab状态
    isShowTab(state) {
        return state.showTab
    },

    /* 方法名随意,主要是来承载变化的showFooter的值 */
    isShow(state) {
        return state.showFooter
    },
    /* 方法名随意,主要是用来承载变化的changableNum的值 */
    getChangedNum(state) {
        return state.changableNum
    },
    getUserData(state) {
        return state.userData
    },
    getPath(state) {
        return state.paht
    }
};
/* 自定义同步改变state初始值的方法 */
const mutations = {
    // @decs 改变页头标题状态
    newTitle(state, data) {
        state.titleHeader = data;
    },
    // @decs 显示Tab
    newShowTab(state) {
        state.showTab = true;
    },
    // @decs 隐藏Tab
    newHideTab(state) {
        state.showTab = false;
    },

    /* 这里面的参数除了state之外还可以再传额外的参数(变量或对象); */
    show(state) {
        state.showFooter = true;
    },
    /* 同上 */
    hide(state) {
        state.showFooter = false;
    },
    /* 同上，这里面的参数除了state之外还传了需要增加的值sum */
    newNum(state, sum) {
        state.changableNum += sum;
    },
    /* 用户信息 */
    newUserData(state, data) {
        state.userData = data;
    },
    /* 面包屑路径 */
    newPath(state, data) {
        state.paht = data;
    }
};
/* 自定义异步触发mutations里函数的方法， */
const actions = {
    /* context与store 实例具有相同方法和属性 */
    titleFooter(context, data) {
        context.commit('newTitle', data)
    },
    showTabFooter(context) {
        context.commit('newShowTab');
    },
    hideTabFooter(context) {
        context.commit('newHideTab');
    },
    hideFooter(context) {
        context.commit('hide');
    },
    /* 同上注释 */
    showFooter(context) {
        context.commit('show');
    },
    /* 同上注释，num为要变化的形参 */
    getNewNum(context, num) {
        context.commit('newNum', num)
    },
    userFooter(context, data) {
        context.commit("newUserData", data)
    },
    pathFooter() {
        context.commit("newPath", data)
    }
};

export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions,
    plugins: [createPersistedState({
        // storage: window.sessionStorage,
        // reducer(val) {
        //     return {
        //         // 只储存state中的assessmentData
        //         changableNum: val.changableNum
        //     }
        // }
    })]
})